// styling
import styles from './style.module.scss';

// components
import LazyImage from '@components/LazyImage';
import Spring from '@components/Spring';

// utils
import classNames from 'classnames';

// assets
import metamask from '@assets/wallets/metamask.webp';
import coinbase from '@assets/wallets/coinbase.webp';
import glow from '@assets/wallets/glow.webp';
import phantom from '@assets/wallets/phantom.webp';
import walletConnect from '@assets/wallets/walletconnect.webp';
import bitski from '@assets/wallets/bitski.webp';

// wagmi

import { useState } from 'react';
import { useAccount, useConnect, useEnsName, useDisconnect } from 'wagmi'
import { InjectedConnector } from 'wagmi/connectors/injected'

const Wallet = () => {
  const wallets = [
    {
      name: "MetaMask",
      image: metamask,
      tag: "popular",
    },
    // {
    //     name: 'Coinbase Wallet',
    //     image: coinbase,
    // },
    // {
    //     name: 'WalletConnect',
    //     image: walletConnect,
    // },
  ];

  const { account, connect, connectors, error, isLoading, pendingConnector } =
    useConnect();

    const {disconnect} = useDisconnect();

  const [isDisconnectOpen, setIsDisconnectOpen] = useState(false);

  
  return (
    <section className="mt-0">
      <div className="container">
        <div className={`${styles.wrapper} bg-secondary border-10`}>
          <div className="d-flex flex-column g-15">
            <h3>You need an Ethereum wallet to use VoidGate</h3>
            <p className={`${styles.subtitle} h6`}>
              Connect with one of our available wallet providers or create a new
              one
            </p>
          </div>

          {connectors.map((connector) => (
            <div
              className={styles.main}
              onClick={() => connect({ connector })}
              key={connector.name}
            >
              {wallets.map((wallet, index) => (
                <Spring key={index} index={index}>
                  <div
                    className={`${styles.main_item} border-hover border-hover--horizontal`}
                    disabled={!connector.ready}
                  >
                    <LazyImage
                      className={styles.img}
                      src={wallet.image}
                      alt={wallet.name}
                    />

                    <h5 className="main_item-title text-overflow">
                      {connector.name}
                    </h5>
                    {wallet.tag && (
                      <span
                        className={classNames(`${styles.label} label-tag`, {
                          accent: wallet.tag === "popular",
                        })}
                      >
                        {wallet.tag}
                      </span>
                    )}
                  </div>
                </Spring>
              ))}
            </div>
          ))}

          {/* <div className="d-flex justify-content-end">
            <div className="dropdown">
              <button
                className="btn btn-primary dropdown-toggle"
                type="button"
                id="disconnectDropdown"
                onClick={() => setIsDisconnectOpen(!isDisconnectOpen)}
              >
                Disconnect
              </button>
              {isDisconnectOpen && (
                <ul className="dropdown-menu" aria-labelledby="disconnectDropdown">
                  <li onClick={disconnect}>Disconnect</li>
                </ul>
              )}
            </div>
          </div> */}
        </div>
        </div>
        </section>
 );
   };
   
  export default Wallet;


